@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-light.woff2") format("woff2"), url("../fonts/neuemachina-light.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-regular.woff2") format("woff2"), url("../fonts/neuemachina-regular.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-medium.woff2") format("woff2"), url("../fonts/neuemachina-medium.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 900;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-ultrabold.woff2") format("woff2"), url("../fonts/neuemachina-ultrabold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

body {
  width: 100%;
  height: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Neue Machina", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.page-body {
  overflow-x: hidden;
  width: 100%; }

.wrapper {
  position: relative; }
  .wrapper--no-scrollbar {
    height: 100vh;
    height: calc(100 * var(--vh, 1vh));
    -ms-overflow-style: none;
    scrollbar-width: none; }
    .wrapper--no-scrollbar::-webkit-scrollbar {
      display: none; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.scroll-lock {
  position: fixed;
  overflow-y: auto; }

.container {
  width: 100%;
  max-width: 1024px;
  margin: 0 auto;
  padding: 0 35px; }

.header {
  display: block;
  padding: 25px 0; }

.loader {
  padding-top: 334px;
  padding-bottom: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff;
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  min-height: 100vh;
  z-index: 1; }
  @media (max-width: 1023px) {
    .loader {
      padding-top: 477px;
      padding-bottom: 80px; } }
  .loader svg {
    margin-bottom: 208px;
    animation: bounce 0.6s; }
    @media (max-width: 1023px) {
      .loader svg {
        width: 550px;
        height: 70px;
        margin-bottom: 352px; } }
  .loader button {
    border: none;
    background-color: rgba(255, 255, 255, 0);
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 22px;
    color: #000000; }

@media (max-width: 1023px) {
  .loader__decktop-text {
    display: none; } }

.loader__modile-text {
  display: none; }
  @media (max-width: 1023px) {
    .loader__modile-text {
      display: inline; } }

.loader--animation {
  animation: skip 0.6s;
  animation-fill-mode: forwards; }

@keyframes bounce {
  0% {
    transform: translateY(-2000px); }
  70% {
    transform: translateY(30px); }
  90% {
    transform: translateY(-10px); }
  100% {
    transform: translateY(0); } }

@keyframes skip {
  0% {
    transform: translateX(0); }
  99% {
    transform: translateX(100vw); }
  100% {
    transform: translateX(100vw);
    transform: scale(0); } }

.page-main {
  overflow-x: hidden; }

.page-main__block {
  display: flex;
  position: relative;
  min-height: 100vh; }

.page-maim__title {
  font-style: normal;
  font-weight: normal;
  font-size: 96px;
  line-height: 96px;
  color: #2c39f2;
  margin: 0;
  margin-top: 99px;
  margin-bottom: 100px;
  max-width: 600px; }
  @media (max-width: 1023px) {
    .page-maim__title {
      margin-top: 227px; } }

.page-main__content {
  padding-top: 40px;
  padding-left: 2px; }
  @media (max-width: 1023px) {
    .page-main__content {
      display: none;
      flex-direction: column;
      align-items: center;
      position: absolute;
      padding-top: 155px;
      top: 0;
      left: calc(-100vw - 35px);
      width: 100vw;
      min-height: 100vh;
      background-color: #2c39f2;
      z-index: 10; }
      .page-main__content--show {
        display: flex;
        animation: to-right 0.6s;
        animation-fill-mode: forwards; }
      .page-main__content--close {
        animation: to-left 0.6s;
        animation-fill-mode: forwards;
        display: flex; } }

@media (max-width: 1023px) {
  .page-main__knight {
    display: none; } }

.page-main__knight-white {
  display: none; }
  @media (max-width: 1023px) {
    .page-main__knight-white {
      display: inline; } }

.page-main__description {
  font-style: normal;
  font-weight: normal;
  font-size: 30px;
  line-height: 40px;
  color: #000000;
  margin: 0;
  margin-top: 26px; }
  @media (max-width: 1023px) {
    .page-main__description {
      color: #ffffff;
      max-width: 350px;
      margin-top: 32px; } }

@keyframes to-right {
  0% {
    transform: translateX(0); }
  100% {
    transform: translateX(100vw); } }

@keyframes to-left {
  0% {
    transform: translateX(100vw); }
  100% {
    transform: translateX(0); } }

